@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// Header
.white-theme {
  .header {
    .logo-dark {
      display: none;
    }
    .logo-white {
      display: block;
    }
    .nav-menu {
      > ul {
        > li {
          > a {
            color: var(--whiteColor);
            @media (max-width: 992px) {
              color: var(--primary);
            }
            &:after {
              background-color: var(--whiteColor);
            }
          }
        }
      }
    }
    &__btns {
      .btn-outline {
        color: var(--whiteColor);
        @media (max-width: 992px) {
          color: var(--primary);
          &:hover {
            color: var(--whiteColor);
          }
        }
      }
      .btn {
        &:hover {
          color: var(--whiteColor);
        }
        @media (max-width: 992px) {
          &:hover {
            color: var(--primary);
          }
        }
      }
    }
    .burger {
      color: var(--whiteColor);
      &--active {
        color: var(--primary);
      }
    }
    &.backgroundWhite {
      background-color: rgba(0, 0, 0, 0.7);
    }
    .header-profile__profile {
      color: var(--whiteColor);
      &:after {
        content: "";
        border: 2px solid var(--whiteColor);
      }
    }
  }
}
.header {
  padding: 30px 0;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  transition: all 0.3s ease 0s;
  z-index: 99;
  // header__flex

  &__flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 99;
    .logo-white {
      display: none;
    }
    @media (max-width: 480px) {
      img {
        max-width: 80%;
      }
    }
  }

  // .header__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }

  // .header__nav

  &__nav {
  }

  // .header__btns

  &__btns {
    margin-left: 60px;
    @media (max-width: 992px) {
      position: fixed;
      left: 0;
      bottom: 20px;
      display: flex;
      z-index: 100;
      margin-left: 0;
      width: 100%;
      justify-content: center;
      visibility: hidden;
      opacity: 0;
      transition: all 0.3s ease 0s;

      &.active {
        visibility: visible;
        opacity: 1;
      }
    }
    @media (max-width: 480px) {
      flex-direction: column;
      align-items: center;
    }
  }

  // .header__btn-outline

  &__btn-outline {
    margin-right: 10px;
    @media (max-width: 1200px) {
      padding: 5px 20px;
    }
    @media (max-width: 992px) {
      border-radius: 25px;
    }
    @media (max-width: 480px) {
      width: 90%;
      text-align: center;
      margin-bottom: 10px;
      margin-right: 0;
      line-height: 120%;
    }
  }

  // .header__btn

  &__btn {
    @media (max-width: 1200px) {
      padding: 5px 20px;
    }
    @media (max-width: 992px) {
      border-radius: 25px;
    }
    @media (max-width: 480px) {
      width: 90%;
      text-align: center;
      line-height: 120%;
    }
  }

  .nav-menu {
    > ul {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      position: relative;
      z-index: 100;
      @media (max-width: 992px) {
        width: 100%;
        height: 100vh;
        transition: all 0.3s ease 0s;
        background-color: var(--lightGray);
        display: none;
      }

      > li {
        margin-right: 30px;
        @media (max-width: 1200px) {
          margin-right: 20px;
        }
        &:last-child {
          margin-right: 0;
        }
        > a {
          font-weight: 600;
          line-height: 30px;
          text-align: center;
          text-transform: uppercase;
          color: var(--primary);
          position: relative;
          @media (max-width: 1200px) {
            font-size: 15px;
          }
          &:after {
            content: "";
            position: absolute;
            left: 0;
            bottom: -2px;
            width: 100%;
            height: 2px;
            background-color: var(--primary);
            transform: scale(0);
            transition: all 0.3s ease 0s;
          }
          &:hover:after {
            transform: scale(1);
          }
        }
      }
    }
    &.menu--active {
      > ul {
        @media (max-width: 992px) {
          display: flex;
        }
      }
    }
    //display: none;
    @media (max-width: 992px) {
      > ul {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        > li {
          margin: 0;
          margin-bottom: 15px;
          &:last-child {
            margin-bottom: 0;
          }
          > a {
            font-size: 25px;
            @media (max-width: 480px) {
              font-size: 20px;
            }
            &:hover:after {
              transform: scale(0);
            }
          }
        }
      }
    }
    @media (max-height: 350px) {
      > ul {
        > li {
          margin-bottom: 0px;
          > a {
            font-size: 16px;
          }
        }
      }
    }
  }
  &.backgroundWhite {
    background-color: var(--whiteColor);
    box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.1);
  }
  @media (max-width: 992px) {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

// HEADER PROFILE

.header-profile {
  // .header-profile__prof
  &__prof {
    margin-left: 60px;
    display: flex;
    gap: 20px;
    @media (max-width: 1400px) {
      margin-right: 20px;
    }
    @media (max-width: 576px) {
      margin-left: 15px;
      margin-right: 15px;
    }
  }

  // .header-profile__avatar

  &__avatar {
    cursor: pointer;
    @media (max-width: 576px) {
      img {
        width: 40px;
        height: 40px;
      }
    }
  }

  // .header-profile__wrap

  &__wrap {
    display: flex;
    align-items: center;
    position: relative;
  }

  // .header-profile__profile

  &__profile {
    font-weight: 600;
    line-height: 30px;
    text-transform: uppercase;
    color: var(--primary);
    position: relative;
    &:after {
      content: "";
      position: absolute;
      top: 8px;
      right: -20px;
      transform: rotate(45deg);
      width: 8px;
      height: 8px;
      background-color: transparent;
      border: 2px solid var(--primary);
      border-left: none;
      border-top: none;
      transform-origin: center;
      transition: all 0.3s ease 0s;
      @media (max-width: 576px) {
        right: 5px;
        width: 6px;
        height: 6px;
      }
    }
    &.open:after {
      transform: rotate(225deg);
      top: 11px;
    }

    @media (max-width: 576px) {
      font-size: 0;
    }
  }

  // .header-profile__list

  &__list {
    position: absolute;
    top: 110%;
    left: -50px;
    background: var(--whiteColor);
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
    width: 110px;
    text-align: center;
    border-radius: 8px;
    display: none;
    @media (max-width: 576px) {
      left: -65px;
    }
    > li {
      width: 100%;
      padding: 6px 8px;
      transition: all 0.3s ease 0s;
      &:first-child {
        border-radius: 8px 8px 0 0;
      }
      &:last-child {
        margin-bottom: 0;
        border-radius: 0 0 8px 8px;
      }
      &:hover {
        background-color: #f7e3d2;
      }
      > a {
        text-transform: uppercase;
        color: var(--primary);
        transition: all 0.3s ease 0s;
        display: block;
        text-transform: capitalize;
      }
    }
    &.open {
      display: block;
    }
  }
}

