// SECTION CAT

.cat {
  padding-top: 150px;
  padding-bottom: 160px;
  @media (max-width: 1200px) {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  @media (max-width: 768px) {
    padding-top: 70px;
    padding-bottom: 70px;
  }
  @media (max-width: 576px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
  // .cat__wrap

  &__wrap {
  }

  // .cat__title

  &__title {
    text-align: center;
  }

  // .cat__subtitle

  &__subtitle {
    max-width: 1120px;
    margin: 20px auto 0 auto;
    line-height: 26px;
    text-align: center;
    @media (max-width: 576px) {
      font-size: 14px;
    }
  }

  // .cat__items

  &__items {
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    @media (max-width: 768px) {
      flex-wrap: wrap;
      justify-content: center;
    }
    @media (max-width: 576px) {
      margin-top: 35px;
    }
  }

  // .cat__item

  &__item {
    flex: 0 1 430px;
    cursor: pointer;
    position: relative;
    transition: all 0.3s ease 0s;
    box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.05);
    &:hover .cat__img {
      transform: scale(1.03);
      @media (max-width: 768px) {
        transform: none;
      }
    }
    &:hover .cat__name {
      color: var(--secondary);
      background-image: url("../img/arrow-cat-hover.svg");
      background-size: 24px 17px;
      background-repeat: no-repeat;
      background-position: 100% center;
      @media (max-width: 1200px) {
        background-size: 20px 15px;
      }
      @media (max-width: 992px) {
        background-size: 17px 13px;
      }
    }
  }

  // .cat__img

  &__img {
    transition: all 0.3s ease 0s;
  }

  // .cat__content

  &__content {
    position: absolute;
    left: 30px;
    bottom: 30px;
    padding-right: 15px;
    @media (max-width: 1200px) {
      left: 20px;
      bottom: 20px;
      height: 90px;
    }
    @media (max-width: 992px) {
      left: 15px;
      bottom: 15px;
      height: 60px;
    }
    @media (max-width: 576px) {
      height: 70px;
    }
  }

  // .cat__name

  &__name {
    font-weight: 800;
    font-size: 24px;
    line-height: 30px;
    text-transform: uppercase;
    color: var(--whiteColor);
    transition: all 0.2s ease 0s;
    display: inline-block;
    padding-right: 35px;
    @media (max-width: 1200px) {
      font-size: 20px;
      line-height: 25px;
    }
    @media (max-width: 992px) {
      font-size: 15px;
      line-height: 20px;
      margin-top: auto;
    }
    @media (max-width: 768px) {
      font-size: 20px;
    }
    @media (max-width: 576px) {
      font-size: 15px;
    }
  }

  // .cat__text

  &__text {
    font-weight: 400;
    font-size: 15px;
    line-height: 22px;
    color: var(--whiteColor);
    margin-top: 5px;
    @media (max-width: 1200px) {
      font-size: 12px;
      line-height: 20px;
    }
    @media (max-width: 992px) {
      font-size: 10px;
      line-height: 13px;
    }
    @media (max-width: 768px) {
      font-size: 13px;
      line-height: 18px;
    }
    @media (max-width: 576px) {
      font-size: 12px;
      line-height: 15px;
    }
  }
}

