        transition: all 0.3s ease 0s;
        @media (max-width: 992px) {
          width: 6px;
          height: 6px;
          border-width: 2px;
          top: 5px;
          right: -20px;
        }
      }
      &.open {
        &:after {
          transform: rotate(225deg) translateY(-7px);
          @media (max-width: 576px) {
            top: 4px;
          }
        }
      }
    }

    // .accordion__content

    &__content {
      position: relative;
      opacity: 0;
      visibility: hidden;
      overflow: hidden;
      max-height: 0;
      transition: all 0.3s ease-out 0s;
      box-sizing: content-box;
      &.open {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  .calories {
    padding-top: 17px;
    display: flex;
    align-items: center;
    gap: 10px;
    @media (max-width: 1400px) {
      flex-wrap: wrap;
    }
    @media (max-width: 576px) {
      flex-wrap: wrap;
    }
    // .calories__item

    &__item {
      width: 80px;
      height: 90px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      background-color: var(--whiteColor);
      text-align: center;
      @media (max-width: 576px) {
        width: 60px;
        height: 70px;
      }
    }

    // .calories__name

    &__name {
      font-weight: 600;
      font-size: 13px;
      line-height: 22px;
      @media (max-width: 576px) {
        font-size: 12px;
        line-height: 15px;
      }
    }

    // .calories__meaning

    &__meaning {
      font-weight: 900;
      font-size: 18px;
      line-height: 22px;
      color: var(--secondary);
      margin-top: 4px;
      @media (max-width: 576px) {
