  // .ingredients__left

  &__left {
    > ul {
      list-style: none;
      padding: 0;
      margin: 0;
      margin-top: 20px;
      > li {
        padding-left: 18px;
        position: relative;
        margin-bottom: 5px;
        @media (max-width: 1300px) {
          width: 400px;
        }
        @media (max-width: 420px) {
          width: 100%;
          font-size: 14px;
        }
        &:last-child {
          margin-bottom: 0;
        }
        &:after {
          content: "";
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background-color: var(--secondary);
