    font-size: 24px;
    line-height: 30px;
    text-transform: uppercase;
  }

  // .ingredients__right

  &__right {
    > ol {
      counter-reset: li;
      padding-left: 0;
      max-width: 890px;
      margin-top: 20px;
      @media (max-width: 1300px) {
        max-width: 100%;
      }
      > li {
        position: relative;
        list-style: none;
        padding-left: 40px;
        font-size: 15px;
        line-height: 25px;
        margin-bottom: 25px;
        &:last-child {
          margin-bottom: 0;
        }
        &:before {
          content: counter(li) ".";
          counter-increment: li;
          position: absolute;
          top: 2px;
          left: 0;
          width: 50px;
          height: 50px;
          font-weight: 900;
          font-size: 18px;
          line-height: 25px;
