        position: absolute;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
      }

      // .custom-checkbox__content

      &__content {
        position: absolute;
        width: 1em;
        height: 1em;
        opacity: 0.5;
        border: 1px solid #cccccc;
        border-radius: 2px;
        margin-left: -20px;
      }
    }
    .custom-checkbox__field:checked + .custom-checkbox__content {
      background-color: var(--secondary);
    }

    input[type="checkbox"]:checked + span:after {
      content: "";
      position: absolute;
    }

    input[type="checkbox"]:checked + span:after {
      left: 1px;
      top: 2px;
      width: 10px;
      height: 6px;
      border-radius: 1px;
      border-left: 3px solid var(--whiteColor);
      border-bottom: 3px solid var(--whiteColor);
      -webkit-transform: rotate(-45deg);
      -moz-transform: rotate(-45deg);
      -o-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
      transform: rotate(-45deg);
    }

  }
  // .membership__btn

  &__btn {
    margin-top: 40px;
    width: 100%;
  }

  .label__wrap {
    @media (max-width: 650px) {
      justify-self: flex-end;
    }
  }

  .radio {
    input[type="radio"] {
      position: absolute;
      opacity: 0;
      + .radio-label {
        &:before {
          content: '';
          background: var(--whiteColor);
          border-radius: 100%;
          border: 1px solid #E5E5E5;
          display: inline-block;
          width: 40px;
          height: 40px;
          position: relative;
          vertical-align: top;
          cursor: pointer;
          text-align: center;
          transition: all 250ms ease;
          @media (max-width: 480px) {
            width: 20px;
            height: 20px;
          }
        }
      }
      &:checked {
        + .radio-label {
          &:before {
            background-color: var(--secondary);
            box-shadow: inset 0 0 0 10px var(--whiteColor);
            @media (max-width: 480px) {
              box-shadow: inset 0 0 0 4px var(--whiteColor);
            }
          }
        }
      }
      &:focus {
        + .radio-label {
          &:before {
            outline: none;
            border-color: none;
          }
        }
      }
      &:disabled {
        + .radio-label {
          &:before {
            box-shadow: inset 0 0 0 4px green;
            border-color: red;
            background: yellow;
          }
        }
      }
      + .radio-label {
        &:empty {
          &:before {
            margin-right: 0;
          }
        }
      }
    }
  }
  .custom-checkbox {

    // .custom-checkbox__field

    &__field {}

    // .custom-checkbox__content

    &__content {}
  }
}


